<template>
  <div class="menu-wrapper" v-for="menu in menuData" :key="menu.key">
    <el-sub-menu v-if="!validatenull(menu.children)" :index="menu.key">
      <template #title>
        <img
          class="menu-icon"
          :src="$route.name == menu.key ? menu['iconSelected'] : menu['icon']"
        />
        <span>{{ $t("menu." + menu.key) }}</span>
      </template>
      <sidebar-menu-item :menu-data="menu.children"></sidebar-menu-item>
    </el-sub-menu>
    <el-menu-item v-else :index="menu.key" @click="handleClick(menu.path)">
      <img
        class="menu-icon"
        :src="$route.name == menu.key ? menu['iconSelected'] : menu['icon']"
      />
      <span class="menu-item">{{ $t("menu." + menu.key) }}</span>
    </el-menu-item>
  </div>
</template>

<script setup>
import { defineProps, defineComponent } from "vue";
import { validatenull } from "@/utils/validate";
// import sidebarItem from "@/components/sidebarMenu/components/SidebarMenuItem.vue";
import { useRouter } from "vue-router";
const props = defineProps({
  menuData: {
    default: () => [],
    type: Array,
  },
});

const router = useRouter();
const handleClick = (path) => {
  router.push({
    path: path,
  });
};
</script>
<script>
// 使用 defineComponent 显式定义组件名称
export default defineComponent({
  name: "SidebarMenuItem",
});
</script>
<style scoped>
.menu-icon {
  display: flex;
  width: auto;
  align-items: center;
  height: 35%;
  object-fit: contain;
  -o-object-fit: contain;
  margin-right: 0.5em;
}

.menu-item {
  border-left: 3px solid #000000;
  padding-left: 3px;
}
</style>
